.IndicatorsScreen {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  & a {
    border: 1px solid black;
    height: 150px;
    display: flex;
    flex: 50%;
    flex-direction: column;
    align-content: center;
    align-items: center;
    justify-content: center;
    vertical-align: center;
    color: blue;
    text-decoration: underline;
  }
}